<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>dependency</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>groupId</span><span class="token punctuation">></span></span>com.yhtframework.dao<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>groupId</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>artifactId</span><span class="token punctuation">></span></span>yht-dao-mongo<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>artifactId</span><span class="token punctuation">></span></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>scope</span><span class="token punctuation">></span></span>provided<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>scope</span><span class="token punctuation">></span></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>dependency</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br></div></div><p>Mavend scope的几个属性值使用场景</p>
<ul>
<li>
<p>compile
编译范围，默认的scope，编译和打包都有效，会包含在war包中。</p>
</li>
<li>
<p>provided(已提供范围)
只在编译中加载使用，不会包含在目标包中。</p>
</li>
<li>
<p>runtime</p>
</li>
<li>
<p>test
测试范围，在编译环境加入classpath，打包时不会加入。</p>
</li>
<li>
<p>system
系统范围，需要通过systemPath指定本地jar文件路径，避免使用。</p>
</li>
</ul>
<p>父模块中可以声明<code>&lt;dependencyManagement&gt;</code>，这个标签不会真的引入依赖，只是定义公用的坐标，子模块继承后仍然需要指定<code>&lt;groupId&gt;</code>和<code>&lt;artifactId&gt;</code>，可省略<code>&lt;version&gt;</code>标签</p>
